* {
  box-sizing: border-box;
}

body {
  font-family: "Gotham A", Helvetica, Arial, sans-serif;
  font-weight: normal;
  margin: 0;
  overflow-x: hidden;
  background-color: #dfdfe4;
}

main {
  overflow: hidden;
}

h4 {
  position: absolute;
  left: 7%;
  top: 0;
  font-weight: normal;
}

.opaque--false > span {
  opacity: 0;
}

.opaque--true > span {
  opacity: 1;
}

@-webkit-keyframes move {
  from {
    transfrom: translateY(0px);
    opacity: 0;
  }
  to {
    transform: translateY(20px);
    opacity: 1;
  }
}

.move {
  -webkit-animation: move 1s ease 2.5s;
}



